import React from 'react'
import Link from 'gatsby-link'
import App from '../components/App';
import Helmet from "react-helmet";
import NavbarFixed from '../components/NavbarFixed';
import '../styles/blog.css';

const styles = {
  description: {
    color: '#576366',
  },
};

export default function BlogIndex({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="blog-posts content">
      <NavbarFixed
        title="BLOG"
        canGoBack={true}
        backToUrl='/'
      />

      {posts
        .filter(post => post.node.frontmatter.title.length > 0)
        .map(({ node: post }) => {
          return (
            <div className="row" key={post.id}>
              <div className="blog-post-preview col-md-8 offset-md-2">
                  <Link
                    style={{ color: 'black'}}
                    className="blog-post-preview-link"
                    to={post.frontmatter.path}>
                    <h4>
                      {post.frontmatter.title}
                    </h4>
                    <h6 style={styles.description}>{post.frontmatter.date}</h6>
                    <p style={styles.description}>{post.frontmatter.excerpt}</p>
                  </Link>
              </div>
            </div>
          );
        })}
    </div>
  );
}

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            excerpt
          }
        }
      }
    }
  }
`;



